
import {mapGetters, mapMutations} from 'vuex'
import Loading from 'vux/src/components/loading/index.vue'

export default {
  name: 'app',
  components: {
    Loading
  },
  created () {
    this.getAuthorInfo(this) // 获取用户登录信息
    console.log(this.user)
  },
  computed: {
    ...mapGetters({
      pageLoading: 'pageLoading',
      user: 'getUserInfo'
    })
  },
  methods: {
    ...mapMutations({
      getAuthorInfo: 'getAuthorInfo'
    })
  }
}



